import Vue from 'vue';
import Router from 'vue-router';

import accountProxy from '@/components/AccountProxy';


Vue.use(Router);


export default new Router({
  routes: [
    {
     path: '/',
     name: 'home',
     component:  accountProxy ,
    }
  ],
});



